export const saveCurrentStartClass = (label, ep, uri) => ({
    type: 'CURRENT_START_CLASS',
    label: label,
    ep: ep,
    uri: uri,
})

export const saveCurrentEndClass = (label, ep, uri) => ({
    type: 'CURRENT_END_CLASS',
    label: label,
    ep: ep,
    uri: uri,
})

export const saveCurrentEntryPoint = text => ({
    type: 'CURRENT_ENTRY_POINT',
    text: text
})



