import React from 'react'
import SearchStartClass from '../containers/SearchStartClass'
import StartClassList from '../containers/StartClassList'
import EndClassBlock from '../containers/EndClassBlock'

const Eplesscs = () => (
    <div className="search">
        <div className="left">
            <div className="search_box">
                <span className="search_ttl">Select a start class</span>
                <SearchStartClass />
            </div>
            <div className="search_data">
                <StartClassList />
            </div>
        </div>
        <div className="right">
            <EndClassBlock />
        </div>
    </div>
)

export default Eplesscs



