import React, {Component} from 'react'
import {connect} from 'react-redux'
import EndClassItem from '../containers/EndClassItem'
import Loading from '../components/Loading'
import Error from '../components/Error'

class EndClassBlock extends Component {
    constructor(props) {
        super(props);
        this.state = {
            classesList: []
        }
    }

    componentWillReceiveProps(nextProps) {
        this.setState({classesList: nextProps.EndClasses});
    }

    render() {
        return (
            <div>
                <div className="search_box">
                    <span className="search_ttl search_ttl--brown">Select an end class</span>
                    {
                        (() => {
                            if (this.props.EndClasses.length === 0) {
                                return;
                            } else {
                                return (<input placeholder="&#xF002; Enter a keyword" type="text" ref="theEndSearchInput" onKeyUp={e => this.onKeyUp(e)}/>)
                            }
                        })()
                    }
                </div>
                <div className="search_data">
                    {
                        (() => {
                            if (this.props.hasError) {
                                return <Error />
                            } else if (this.props.isLoading) {
                                return <Loading />
                            } else if (this.props.EndClasses.length === 0) {
                                return;
                            } else {
                                return (<ul>
                                    {
                                        this.state.classesList.map((value, i) => {
                                            return <EndClassItem key={'end-' + i} id={i} {...value} />
                                        })
                                    }
                                </ul>)
                            }
                        })()
                    }
                </div>
            </div>
        )
    }

    onKeyUp(e) {
        const node = this.refs.theEndSearchInput
        const searchText = node.value.toLowerCase();

        if (searchText !== '') {
            // フィルター後の配列
            let filteredClasses = [];
            this.props.EndClasses.map((value, i) => {
                const label = value.label.toLowerCase();
                if (label.indexOf(searchText) !== -1) {
                    filteredClasses[i] = value;
                }
            })

            // 絞り込み済のclasses
            this.setState({classesList: filteredClasses});
        }
        // キーワードが空のときは全件表示
        else{
            this.setState({classesList: this.props.EndClasses});
        }
    }
}

const mapStateToProps = function(state, ownProps){
    return {
        EndClasses: state.EndClasses,
        hasError: state.getEndClassError,
        isLoading: state.loadEndClass,
    }
}

const mapDispatchToProps = (dispatch, ownProps) => ({})

const EndClassBlockContainer = connect(
    mapStateToProps,
    mapDispatchToProps
)(EndClassBlock)

export default EndClassBlockContainer



