import { combineReducers } from 'redux'
import {StartClasses, getStartClassError, loadStartClass, isInitStartClass} from './StartClasses'
import {EndClasses, getEndClassError, loadEndClass} from './EndClasses'
import {ResultClasses, getResultClassError, loadResultClass} from './ResultClasses'
import {currentClass} from './CurrentClasses'
import searchKeyword from './searchKeyword'
import {Sparql} from './Sparql'

const SPAQLApp = combineReducers({
  StartClasses,
  getStartClassError,
  loadStartClass,
  isInitStartClass,

  EndClasses,
  getEndClassError,
  loadEndClass,

  ResultClasses,
  getResultClassError,
  loadResultClass,

  currentClass,

  searchKeyword,

  Sparql,
})

export default SPAQLApp



