let config = {
    API_URL: 'http://www.sparqlbuilder.org',
    API_INIT_START_URL: 'http://www.sparqlbuilder.org/api/v2/clist?ex=true',
    API_SEARCH_START_URL: 'http://www.sparqlbuilder.org/api/v2/clist?',
    API_CLIST_URL: 'http://www.sparqlbuilder.org/api/v2/clist',
    API_PLIST_URL: 'http://www.sparqlbuilder.org/api/v2/plist',
    API_SPARQL_URL: 'http://www.sparqlbuilder.org/api/v2/sparql',
}

function get(key) {
    return config[key]
}

export {
    get,
}



