import * as Config from '../config'

export const getStartClassError = status => ({
    type: 'GET_START_CLASS_ERROR',
    hasError: status
})

export const loadStartClass = status => ({
    type: 'LOAD_START_CLASS',
    isLoading: status
})

export const fetchStartClassSuccess = clist => ({
    type: 'FETCH_START_CLASS_SUCCESS',
    clist
})

export const isInitStartClass = status => ({
    type: 'IS_INIT_START_CLASS',
    isInitStartClass: status
})

export function fetchStartClass(text) {
    return (dispatch) => {
        dispatch(loadStartClass(true));

        // リクエストURL
        const params = new URLSearchParams()
        params.set('keyword', text)
        const url = Config.get('API_SEARCH_START_URL') + params.toString()

        fetch(url, {
                mode: 'cors'
            })
            .then((response) => {
                if (!response.ok) {
                    throw Error(response.statusText);
                }
                dispatch(loadStartClass(false));

                return response;
            })
            .then((response) => {
                console.log( response );
                return response.json()
            })
            .then((clist) => {
                console.log( clist );
                dispatch(isInitStartClass(false))
                return dispatch(fetchStartClassSuccess(clist))
            })
            .catch(() => {
                return dispatch(getStartClassError(true))
            });
    }
}



export function fetchInitStartClass() {
    return (dispatch) => {
        dispatch(loadStartClass(true));

        fetch(Config.get('API_INIT_START_URL'))
            .then((response) => {
                if (!response.ok) {
                    throw Error(response.statusText);
                }
                dispatch(loadStartClass(false));

                return response;
            })
            .then((response) => {
                return response.json()
            })
            .then((clist) => {
                dispatch(isInitStartClass(true))
                return dispatch(fetchStartClassSuccess(clist))
            })
            .catch(() => {
                return dispatch(getStartClassError(true))
            });
    }
}



