import { connect } from 'react-redux'
import React, {Component} from 'react'
import {fetchInitStartClass} from '../actions/StartClass'
import * as Config from '../config'
import StartClassItem from '../containers/StartClassItem'
import Loading from '../components/Loading'
import Error from '../components/Error'


class StartClassList extends Component {

    componentWillMount = () => {
        this.props.fetchInitStartClass()
    };

    render() {
        if (this.props.hasError) {
          return <Error />;
        }
        if (this.props.isLoading) {
          return <Loading />;
        }

        // 初期状態はテキスト表示
        let initText = '';
        if (this.props.isInitStartClass) {
            initText = <p className={'initText'}><i className="fa fa-list"></i> Examples of start classes.</p>
        }

        return (
            <div>
                {initText}
                <ul>
                    {this.props.StartClasses.map((value, i) => {
                        return <StartClassItem key={'start-'+i} id={i} {...value} />
                    })}
                </ul>
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => ({
    StartClasses: state.StartClasses,
    hasError: state.getStartClassError,
    isLoading: state.loadStartClass,
    isInitStartClass: state.isInitStartClass,
})

const mapDispatchToProps = (dispatch, ownProps) => ({
    fetchInitStartClass: () => dispatch(fetchInitStartClass())
})

const StartClassListContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(StartClassList)

export default StartClassListContainer



