import React, {Component} from 'react'
import {connect} from 'react-redux'
import { withCookies, Cookies } from 'react-cookie';
import {fetchResultClass} from '../actions/ResultClass'
import ResultClassItem from '../containers/ResultClassItem'
import Loading from '../components/Loading'
import Error from '../components/Error'

class ResultClassList extends Component {
    constructor(props) {
      super(props);
      this.state = {
          ResultClasses: [],
          isPaging: false,
          index: 1,
          per_page: 20,
          is_prev: false,
          is_next: false,
      }
    }

    componentWillMount = () => {
        // URLパラメーターからAPIを検索
        const params = new URLSearchParams(window.location.search)
        const ep = decodeURIComponent( params.get('ep') )
        const st = decodeURIComponent( params.get('st') )
        const en = decodeURIComponent( params.get('en') )
        this.props.fetchResultClass(ep, st, en)

        // Cookie に SPARQLを保存
        const { cookies } = this.props
        cookies.set('sparql-ep', ep, { path: '/' });
    };

    componentWillReceiveProps(nextProps){
        if(nextProps.ResultClasses.length > this.state.per_page){
            this.setState({isPaging: true})

            if( nextProps.ResultClasses.length > ((this.state.index + 1) * this.state.per_page) ){
                this.setState({is_next: true})
            }
        }
    }

    render() {
        // API接続時
        if (this.props.hasError) {
            const style = {
                'textAlign': 'center',
                'color': 'red',
            }
            return (
                <p className={'error error_reload'} onClick={e => this.onReload(e)}>An error occurred. Please click and reload.</p>
            );
        }
        if (this.props.isLoading) {
          return <Loading />;
        }

        // ページング処理
        let ResultClasses = [];
        for (let i = 0; i < this.props.ResultClasses.length; i++) {
            if( this.state.index > 1 ){
                const prev_index = this.state.index - 1;
                if( i < (prev_index * this.state.per_page) || i >= (this.state.index * this.state.per_page) ){
                    continue;
                }else{
                    ResultClasses[i] = this.props.ResultClasses[i]
                }
            }else{
                if( i >= (this.state.index * this.state.per_page) ){
                    break;
                }else{
                    ResultClasses[i] = this.props.ResultClasses[i]
                }
            }
        }

        // Btn
        let prevBtnActive = '';
        if(this.state.is_prev){
            prevBtnActive = 'active'
        }
        const prevBtn = <p className={`node_btn--prev ${prevBtnActive}`} onClick={e => this.onPrev(e)} ><span><i className="fa fa-chevron-left" aria-hidden="true"></i>PREV</span></p>

        let nextBtnActive = '';
        if(this.state.is_next){
            nextBtnActive = 'active'
        }
        const nextBtn = <p className={`node_btn--next ${nextBtnActive}`} onClick={e => this.onNext(e)} ><span>NEXT<i className="fa fa-chevron-right" aria-hidden="true"></i></span></p>

        return (
            <div>
                <ul className="node_list">
                    {
                        ResultClasses.map((value, i) => {
                            return <ResultClassItem
                                key={'result-'+i}
                                id={i}
                                {...value} />
                        })
                    }
                </ul>

                {(() => {
                    if (this.state.isPaging) {
                        return (
                            <div className="btn_group">
                                {prevBtn}
                                {nextBtn}
                            </div>
                        )
                    }
                })()}

            </div>
        )
    }

    onPrev(e){
        e.preventDefault();
        const index = this.state.index - 1;

        if(this.state.is_prev){
            this.setPrevNext(index)
        }
    }

    onNext(e){
        e.preventDefault();
        const index = this.state.index + 1;

        if(this.state.is_next){
            this.setPrevNext(index)
        }
    }

    setPrevNext(index){
        let state = {index: index}

        if(index === 1){
            state.is_prev = false
        }else{
            state.is_prev = true
        }

        if( this.props.ResultClasses.length > ((this.state.index + 1) * this.state.per_page ) ){
            state.is_next= true
        }else{
            state.is_next= false
        }

        this.setState(state)
    }

    onReload(e){
        e.preventDefault();
        window.location.reload();
    }

}

const mapStateToProps = (state, ownProps) => {
    return {
        ResultClasses: state.ResultClasses,
        hasError: state.getResultClassError,
        isLoading: state.loadResultClass
    }
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchResultClass: (ep, st, en) => dispatch(fetchResultClass(ep, st, en)),
});

const ResultClassListContainer = connect(
    mapStateToProps,
    mapDispatchToProps
)(ResultClassList)

export default withCookies(ResultClassListContainer)



