import * as Config from '../config'

export const getEndClassError = status => ({
    type: 'GET_END_CLASS_ERROR',
    hasError: status
})

export const loadEndClass = status => ({
    type: 'LOAD_END_CLASS',
    isLoading: status
})

export const fetchEndClassSuccess = clist => ({
    type: 'FETCH_END_CLASS_SUCCESS',
    clist
})

export function fetchEndClass(ep, uri) {
    return (dispatch) => {
        dispatch(loadEndClass(true));
        dispatch(getEndClassError(false))

        const api_url = Config.get('API_CLIST_URL') + "?ep=" + encodeURIComponent(ep) + '&class=' + encodeURIComponent(uri)
        console.log( api_url )

        fetch(api_url)
            .then((response) => {
                if (!response.ok) {
                    throw Error(response.statusText);
                }
                dispatch(loadEndClass(false));

                return response;
            })
            .then((response) => {
                return response.json()
            })
            .then((clist) => {
                // console.log( clist );
                return dispatch(fetchEndClassSuccess(clist))
            })
            .catch(() => {
                return dispatch(getEndClassError(true))
            });
    }
}


export function resetEndClass() {
    return (dispatch) => {
        return dispatch(fetchEndClassSuccess([]))
    }
}



