export const EndClasses = (state = [], action) => {
    switch (action.type) {
        case 'FETCH_END_CLASS_SUCCESS':
            return action.clist
        default:
            return state
    }
}


export const getEndClassError = (state = false, action) => {
    switch (action.type) {
        case 'GET_END_CLASS_ERROR':
            // console.log(action.hasError);
            return action.hasError;
        default:
            return state;
    }
}


export const loadEndClass = (state = false, action) => {
    switch (action.type) {
        case 'LOAD_END_CLASS':
            return action.isLoading;
        default:
            return state;
    }
}



