import React from 'react'
import { BrowserRouter, Route } from 'react-router-dom'
import { CookiesProvider } from 'react-cookie'
import * as Config from '../config'
import Eplesscs from '../components/Eplesscs'
import Select from '../containers/Select'

const App = () => (
        <CookiesProvider>
            <RouteHome />
        </CookiesProvider>
)

const RouteHome = () => {
    const params = new URLSearchParams(window.location.search)
    const ep = decodeURIComponent( params.get('ep') )
    const st = decodeURIComponent( params.get('st') )
    const en = decodeURIComponent( params.get('en') )

    if(params.get('ep') && params.get('st') && params.get('en')){
        return <Select />
    }else{
        return <Eplesscs />
    }
}

export default App



