import React, {Component} from 'react'
import {connect} from 'react-redux'
import {fetchEndClass} from '../actions/EndClass'
import {resetEndClass} from '../actions/EndClass'
import {saveCurrentStartClass} from '../actions/CurrentClass'

class StartClassItem extends Component {

    render() {
        return (
            <li
                className={'search_data_list search_data_list-'+this.props.id}
                data-ep={this.props.ep}
                data-uri={this.props.uri}
                data-label={this.props.label}
                onClick={e => this.onClick(e)}
                >
                <div className="search_data_title">{this.props.label}</div>
                <p className="search_data_link">{this.props.uri}</p>
                <p className="search_data_link search_data_link-class">{this.props.ep}</p>
            </li>
        )
    }

    onClick(e) {
        e.preventDefault();

        // Classを操作
        // ex: https://developer.mozilla.org/ja/docs/Web/API/Element/classList
        // active classを一旦すべて削除
        [].forEach.call(document.getElementsByClassName("search_data_list"), function(val, i) {
            val.classList.remove('active');
        });
        // 該当のものだけにactiveを付ける
        e.currentTarget.classList.add('active');

        // Actionにデータを送る
        const ep = e.currentTarget.dataset.ep;
        const uri = e.currentTarget.dataset.uri;
        // this.props.resetEndClass()
        this.props.fetchData(ep, uri)

        // 選択したStartClassを保存
        const label = e.currentTarget.dataset.label;
        this.props.saveCurrentStartClass(label, ep, uri)

    }
}

const mapStateToProps = (state, ownProps) => ({})

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchData: (ep, uri) => dispatch(fetchEndClass(ep, uri)),
  saveCurrentStartClass: (label, ep, uri) => dispatch(saveCurrentStartClass(label, ep, uri)),
  resetEndClass: () => dispatch(resetEndClass()),
});

const StartClassItemContainer = connect(
    mapStateToProps,
    mapDispatchToProps
)(StartClassItem)

export default StartClassItemContainer



