import React, {Component} from 'react'
import { connect } from 'react-redux'
import { withRouter, router } from 'react-router-dom'
import {saveCurrentEndClass} from '../actions/CurrentClass'

class SBClassItem extends Component {
    constructor(props, context) {
        super(props, context);
    }

    render() {
        return (
            <li className={'search_data_list search_data_list--pink search_data_list-' + this.props.id}
                data-uri={this.props.uri}
                data-ep={this.props.ep}
                data-label={this.props.label}
                onClick={e => this.onClick(e)}
                >
                <div className="search_data_title">{this.props.label} ({this.props.number})</div>
                <p className="search_data_link">{this.props.uri}</p>

            </li>
        )
    }

    onClick(e) {
        e.preventDefault();

        // 選択したEndClassを保存
        const label = e.currentTarget.dataset.label;
        const ep = e.currentTarget.dataset.ep;
        const uri = e.currentTarget.dataset.uri;
        // this.props.saveCurrentEndClass(label, ep, uri)

        // ページ遷移
        const href = './?ep=' + encodeURIComponent(this.props.currentClass.st_ep) + '&st=' + encodeURIComponent(this.props.currentClass.st_uri) + '&en=' + encodeURIComponent(uri);
        // this.props.history.push(href)
        // this.context.router.transitionTo(href);
        window.location.href = href

    }
}

const mapStateToProps = (state, ownProps) => ({
    currentClass: state.currentClass,
})

const mapDispatchToProps = (dispatch, ownProps) => ({
    saveCurrentEndClass: (label, ep, uri) => dispatch(saveCurrentEndClass(label, ep, uri)),
})

const SBClassItemContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(SBClassItem)

export default SBClassItemContainer



