import React, {Component} from 'react'
import {connect} from 'react-redux'
import ResultClassList from '../containers/ResultClassList'
import * as Config from '../config'
import Loading from '../components/Loading'
import Error from '../components/Error'

class Select extends Component {
    constructor(props) {
      super(props);
      this.state = {
          st_obj: {
              label: null
          },
          en_obj: {
              label: null
          },
      }
    }

    componentWillMount = () => {
        // URLパラメーターからAPIを検索
        const params = new URLSearchParams(window.location.search)
        const ep = decodeURIComponent( params.get('ep') )
        const st = decodeURIComponent( params.get('st') )
        const en = decodeURIComponent( params.get('en') )

        // 選択されたST, ENをURLから取得する
        const st_api_url = Config.get('API_CLIST_URL') + "?ep=" + encodeURIComponent(ep)
        this.setListObj('st', st, st_api_url)
        const en_api_url = Config.get('API_CLIST_URL') + "?ep=" + encodeURIComponent(ep) + '&class=' + encodeURIComponent(st);
        this.setListObj('en', en, en_api_url)

        // 選択された各Classを保存
        this.setState({
            ep: ep,
            st: st,
            en: en,
        })
    }

    componentWillReceiveProps(nextProps){
    }

    render() {
        let stLabel
        if(this.state.st_obj.label ===  null){
            stLabel = <Loading width="20px" height="20px" textAlign="left" />
        }else{
            stLabel = this.state.st_obj.label
        }

        let enLabel
        if(this.state.en_obj.label ===  null){
            enLabel = <Loading width="20px" height="20px" textAlign="left" />
        }else{
            enLabel = this.state.en_obj.label
        }

        return (
            <div className="select">
              <div className="data_tbl">
                <div className="left">
                  <table>
                      <tbody>
                    <tr>
                      <th><span className="tbl_left">SPARQL endpoint</span></th>
                      <td>{this.state.ep}</td>
                    </tr>

                    <tr>
                      <th><span className="tbl_left">start class</span></th>
                      <td>{stLabel}</td>
                    </tr>
                    </tbody>
                  </table>
                </div>

                <div className="right">
                  <table>
                      <tbody>
                    <tr>
                      <th>&nbsp;</th>
                      <td>&nbsp;</td>
                    </tr>

                    <tr>
                      <th><span className="tbl_left tbl_left--brown">end class</span></th>
                      <td>{enLabel}</td>
                    </tr>
                    </tbody>
                  </table>
                </div>
              </div>


              <div className="path">
                {(()=>{
                    if (this.props.ResultClasses.length !== 0) {
                        return (<p className="path__numbers">{this.props.ResultClasses.length} Paths found.</p>)
                    }
                })()}
                <span className="path__permalink">
                    <a href="javascript:;" onClick={e => this.showPermalink(e)}>
                        <i className="fa fa-files-o" aria-hidden="true"></i>Permalink
                    </a>
                </span>
              </div>


              <div className="permalink" ref='thePermalink'>
                  <div className="permalink_body">
                      <h3 className="permalink_title">Permalink</h3>
                      <div className="permalink_inner">
                          <input type="text" className="permalink_input" ref='thePermalinkUrl' defaultValue={window.location.href} />
                          <div className="permalink_button-wrap">
                              <button className="permalink_button" onClick={e => this.copyPermalink(e)}>Copy</button>
                          </div>
                          <p className="permalink_close">
                              <a href="javascript:;" onClick={e => this.hidePermalink(e)}>
                                  <i className="fa fa-times"></i> Close
                              </a>
                          </p>
                      </div>
                  </div>
                  <div className="permalink_overlay" ref='theOverlay'></div>
              </div>


              <div className="select_inner">
                  <p>Select <span className="light_green">a path</span> to generate a SPARQL query.</p>
                <ResultClassList />

              </div>
            </div>
        )
    }


    showPermalink(e){
        const node = this.refs.thePermalink
        node.classList.add('show');

        const overlay = this.refs.theOverlay
        overlay.style.height = document.body.clientHeight
    }

    hidePermalink(e){
        const node = this.refs.thePermalink
        node.classList.remove('show');
    }

    copyPermalink(e){
        const node = this.refs.thePermalinkUrl
        node.focus();
        node.select();
        document.execCommand('copy');
        alert('Copied the permalink to clipboard.')
    }


    setListObj(type, uri, api_url){
        fetch(api_url)
            .then((response) => {
                if (!response.ok) {
                    throw Error(response.statusText);
                }
                return response;
            })
            .then((response) => {
                return response.json()
            })
            .then((clist) => {
                for (var i = 0; i < clist.length; i++) {
                    if(clist[i].uri === uri){
                        // ST, ENで切り替える
                        if('st' === type){
                            this.setState({st_obj: clist[i]})
                            return　clist[i];
                        }else if('en' === type){
                            this.setState({en_obj: clist[i]})
                            return　clist[i];
                        }
                    }
                }
            })
            .catch(() => {
                console.log( 'error' );
                return;
            });
    }
}


const mapStateToProps = (state, ownProps) => ({
    currentClass: state.currentClass,
    ResultClasses: state.ResultClasses,
    hasError: state.getResultClassError,
    isLoading: state.loadResultClass,
})

const mapDispatchToProps = (dispatch, ownProps) => ({});

export default connect(mapStateToProps, mapDispatchToProps)(Select)



