import React, {Component} from 'react'
import {connect} from 'react-redux'
import {fetchStartClass} from '../actions/StartClass'
import {resetEndClass} from '../actions/EndClass'

class SearchStartClass extends Component {
    render() {
        return (
            <div className='search_wrap'>
                <input
                    ref='theInput'
                    defaultValue={this.props.searchKeyword}
                    placeholder="&#xF002; Enter a keyword (gene, protein, etc) to search a class"
                    onKeyPress={e => this.onKeyPress(e)}
                 />
                <button onClick={e => this.onClick(e)}>Search</button>
            </div>
        )
    }

    onClick(e) {
        const node = this.refs.theInput
        const text = node.value.trim()
        this.props.fetchData(text)
        this.props.resetEndClass()
    }

    onKeyPress(e) {
        // Enterで検索
        if ( e.which === 13 ) {
            const node = this.refs.theInput
            const text = node.value.trim()
            this.props.fetchData(text)
            this.props.resetEndClass()
        }
    }
}

const mapStateToProps = (state, ownProps) => ({
    searchKeyword: state.searchKeyword
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchData: (text) => dispatch(fetchStartClass(text)),
  resetEndClass: () => dispatch(resetEndClass()),
});

const SearchStartClassContainer = connect(
    mapStateToProps,
    mapDispatchToProps
)(SearchStartClass)

export default SearchStartClassContainer



