export const currentClass = (state = null, action) => {
    switch (action.type) {
        case 'CURRENT_START_CLASS':
            return Object.assign({},
                state,
                {
                  st_label: action.label,
                  st_ep: action.ep,
                  st_uri: action.uri,
                });
        case 'CURRENT_END_CLASS':
            return Object.assign({},
                state,
                {
                    en_label: action.label,
                    en_ep: action.ep,
                    en_uri: action.uri,
                });
        default:
            return state
    }
}



