export const ResultClasses = (state = [], action) => {
    switch (action.type) {
        case 'FETCH_RESULT_CLASS_SUCCESS':
            // console.log(action.plist);
            return action.plist
        default:
            return state
    }
}


export const getResultClassError = (state = false, action) => {
    switch (action.type) {
        case 'GET_RESULT_CLASS_ERROR':
            // console.log(action.hasError);
            return action.hasError;
        default:
            return state;
    }
}


export const loadResultClass = (state = false, action) => {
    switch (action.type) {
        case 'LOAD_RESULT_CLASS':
            // console.log(action.isLoading);
            return action.isLoading;
        default:
            return state;
    }
}



