export const StartClasses = (state = [], action) => {
    switch (action.type) {
        case 'FETCH_START_CLASS_SUCCESS':
            // console.log(action.clist);
            return action.clist
        default:
            return state
    }
}


export const getStartClassError = (state = false, action) => {
    switch (action.type) {
        case 'GET_START_CLASS_ERROR':
            // console.log(action.hasError);
            return action.hasError;
        default:
            return state;
    }
}


export const loadStartClass = (state = false, action) => {
    switch (action.type) {
        case 'LOAD_START_CLASS':
            // console.log(action.isLoading);
            return action.isLoading;
        default:
            return state;
    }
}


export const isInitStartClass = (state = false, action) => {
    switch (action.type) {
        case 'IS_INIT_START_CLASS':
            return action.isInitStartClass;
        default:
            return state;
    }
}



