import * as Config from '../config'

export const getResultClassError = status => ({
    type: 'GET_RESULT_CLASS_ERROR',
    hasError: status
})

export const loadResultClass = status => ({
    type: 'LOAD_RESULT_CLASS',
    isLoading: status
})

export const fetchResultClassSuccess = plist => ({
    type: 'FETCH_RESULT_CLASS_SUCCESS',
    plist
})

export function fetchResultClass(ep, st, en) {
    console.log( 'parem:' + ep + st + en );

    return (dispatch) => {
        dispatch(loadResultClass(true));

        const api_url = Config.get('API_PLIST_URL')
            + "?ep=" + encodeURIComponent(ep)
            + '&startclass=' + encodeURIComponent(st)
            + '&endclass=' + encodeURIComponent(en);
        console.log( api_url )

        fetch(api_url)
            .then((response) => {
                console.log( response );
                if (!response.ok) {
                    throw Error(response.statusText);
                }
                dispatch(loadResultClass(false));

                return response;
            })
            .then((response) => {
                return response.json()
            })
            .then((plist) => {
                dispatch(fetchResultClassSuccess(plist))
                return true;
            })
            .catch((error) => {
                // エラーのときは再度fetch
                // console.log( error.message );
                // reFetchResultClass(ep, st, en)
                return dispatch(getResultClassError(true))
            });
    }
}

function reFetchResultClass(ep, st, en){
    setTimeout( function () {
        fetchResultClass(ep, st, en)
    } , 3000 );
}



