import React, {Component} from 'react'
import {connect} from 'react-redux'
import { withRouter } from 'react-router-dom'
import { withCookies, Cookies } from 'react-cookie';
import * as Config from '../config'
import {saveSPARQL} from '../actions/Sparql'

class ResultClassItem extends Component {
    render() {

        // 結果のリストのHTMLを作成する
        let listHtml = [];
        for (let i = 0; i < this.props.classLinks.length; i++) {
            listHtml.push(<div key={'a'+i}><span className="node_dash"> - </span></div>);

            // predicateを作成
            let propertytext = this.props.classLinks[i].predicate;
            let propertysplit1 = propertytext.split("/");
            let propertysplit2 = propertysplit1[propertysplit1.length - 1];
            let propertysplit3 = propertysplit2.split("#");
            propertytext = propertysplit3[propertysplit3.length - 1];

            listHtml.push(<div key={'b'+i}><span className="node_predicate">{propertytext}</span></div>);
            listHtml.push(<div key={'c'+i}><span className="node_dash"> - </span></div>);

            if( i !== (this.props.classLinks.length - 1) ){
                listHtml.push(<div key={'d'+i}><span className="node gray">{this.props.classLinks[i].label}</span></div>);
            }
            // 最後は緑印にする
            else{
                listHtml.push(<div key={'d'+i}><span className="node green">{this.props.classLinks[i].label}</span></div>);
            }
        }

        // SPARQL Query取得用のAPI URL
        let query_url = 'http://sparqlbuilder.org/api/sparql?path=';
        query_url += '{"startClass":"'+encodeURIComponent(this.props.startClass)+'",';
        query_url += '"label":"'+this.props.label+'",';
        query_url += '"classLinks": [';
        for (var i = 0; i < this.props.classLinks.length; i++) {
            query_url += '{"predicate":"'+encodeURIComponent(this.props.classLinks[i].predicate)+'","linkedClass":"'+encodeURIComponent(this.props.classLinks[i].linkedClass)+'","direction":"'+this.props.classLinks[i].direction+'","label":"'+this.props.classLinks[i].label+'"},';
        }
        query_url += ']}';

        return (
            <li
                className={'node_list_item node_list_item-'+this.props.id}
                onClick={e => this.onClick(e)}
                onMouseOver={e => this.onMouseOver(e)}
                data-queryurl={query_url}
                >
                <div><span className="node blue">{this.props.label}</span></div>
                {listHtml}
            </li>
        )
    }

    onClick(e) {
        e.preventDefault()

        const queryURL = e.currentTarget.dataset.queryurl;

        fetch(queryURL)
            .then((response) => {
                if (!response.ok) {
                    throw Error(response.statusText);
                }
                return response.text();
            })
            .then((text) => {
                console.log( text );
                this.props.saveSPARQL(text)

                // Cookie に SPARQLを保存
                const { cookies } = this.props
                cookies.set('sparql-query', text, { path: '/' });

                // ページ遷移
                // this.props.history.push('/')
                window.location.href = '/'
                return
            })
            .catch((err) => {
                console.log( err );
                return;
            });

    }

    onMouseOver(e){
        // Classを操作
        // active classを一旦すべて削除
        [].forEach.call(document.getElementsByClassName("node_list_item"), function(val, i) {
            val.classList.remove('active');
        });
        // 該当のものだけにactiveを付ける
        e.target.classList.add('active');
    }
}

const mapStateToProps = (state, ownProps) => ({
    SPARQL: state.Sparql,
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  saveSPARQL: (text) => dispatch(saveSPARQL(text))
});

const ResultClassItemContainer = connect(
    mapStateToProps,
    mapDispatchToProps
)(ResultClassItem)

export default withCookies(ResultClassItemContainer)



